  --uh-red-light: #e8354c;
  --background: #0a0a0a;
  --background-secondary: #111111;
  --background-tertiary: #1a1a1a;
  --foreground: #fafafa;
  --foreground-muted: #888888;
  --border: #2a2a2a;
  --border-light: #3a3a3a;
  --success: #22c55e;
  --warning: #f59e0b;
  --glass: rgba(255, 255, 255, 0.03);
  --glass-border: rgba(255, 255, 255, 0.08);
}
* {
  box-sizing: border-box;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: text;
  -moz-user-select: text;
  user-select: text;
}
.btn-primary {
  background: linear-gradient(135deg, var(--uh-red) 0%, var(--uh-red-dark) 100%);
  color: white;
  padding: 14px 28px;
  border-radius: 10px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 20px rgba(200, 16, 46, 0.3),
    0 0 0 1px rgba(200, 16, 46, 0.2);
  position: relative;
  overflow: hidden;
}
.btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(200, 16, 46, 0.4),
    0 0 0 1px rgba(200, 16, 46, 0.3);
}
.btn-primary:hover::before {
  left: 100%;
}
.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.btn-secondary {
  background: var(--glass);
  color: var(--foreground);
  padding: 12px 24px;
  border-radius: 10px;
  font-weight: 500;
  border: 1px solid var(--glass-border);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
}
.btn-secondary:hover {
  background: var(--background-tertiary);
  border-color: var(--border-light);
  transform: translateY(-1px);
}
.card {
  background: linear-gradient(135deg, rgba(17, 17, 17, 0.7) 0%, rgba(26, 26, 26, 0.6) 100%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 28px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.06);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.card:hover {
  border-color: rgba(255, 255, 255, 0.15);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}
.input {
  background: var(--background);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 14px 18px;
  color: var(--foreground);
  font-size: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2);
}
.input:focus {
  outline: none;
  border-color: var(--uh-red);
  box-shadow: 0 0 0 3px rgba(200, 16, 46, 0.15),
    inset 0 2px 4px rgba(0, 0, 0, 0.2);
}
.input::placeholder {
  color: var(--foreground-muted);
}
.status-open {
  color: var(--success);
  text-shadow: 0 0 10px rgba(34, 197, 94, 0.3);
}
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 15px rgba(200, 16, 46, 0.4);
  }
  50% {
    box-shadow: 0 0 30px rgba(200, 16, 46, 0.6);
  }
}
@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.animate-fade-in {
  animation: fade-in 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  opacity: 0;
}
.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}
.hero-gradient {
  background: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(200, 16, 46, 0.25), transparent),
    radial-gradient(ellipse 60% 40% at 100% 0%, rgba(200, 16, 46, 0.1), transparent),
    radial-gradient(ellipse 60% 40% at 0% 100%, rgba(200, 16, 46, 0.08), transparent);
}
header {
  backdrop-filter: blur(20px);
  background: rgba(10, 10, 10, 0.8);
  border-color: var(--glass-border) !important;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--background);
}
::-webkit-scrollbar-thumb {
  background: var(--border);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--border-light);
}
::selection {
  background: rgba(200, 16, 46, 0.3);
  color: var(--foreground);
}
:focus-visible {
  outline: 2px solid var(--uh-red);
  outline-offset: 2px;
}
